.buttonBack {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-decoration: none;
    min-width: 120px;
    margin: 10px;
    padding: 10px;
    border: none;
    color: #ffffff;
    background: transparent;
    outline: 1px solid #ffffff;
}

.buttonBack:hover {
    outline: 2px solid #ffffff;
    color: white;
}

.linkBack {
    text-decoration: none;
}